// new development
const url1 = "https://raw.githubusercontent.com/japmelian/datasets/main/callejero-tenerife.csv";


d3.csv(url1, (err, rows) => {
    function unpack(rows, key) {
        return rows.map(function(row) {
            return row[key]
        })
    };
  
    var data = [
      {
        type: "scattermapbox", lon: unpack(rows, "lon"), lat: unpack(rows, "lat"),
        marker: { size: 4, color: "#0082cd" }
      }
    ];
  
    var layout = {
      mapbox: { style: "open-street-map", zoom: 9, center: { lon: -16.642508, lat: 28.272338 } },
      margin: { r: 0, t: 0, b: 0, l: 0 }
    };
  
    Plotly.newPlot('map', data, layout);
  });